import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import Card from "@material-ui/core/Card";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import Divider from "@material-ui/core/Divider";
import Typography from "@material-ui/core/Typography";

import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";

import Button from "@material-ui/core/Button";
import "./Courses.css";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: "center",
    color: theme.palette.text.secondary,
  },
}));

const useStylesGrid = makeStyles({
  root: {
    maxWidth: 370,
  },
  media: {
    height: 140,
  },
});

export default function Courses(props) {
  const classes = useStyles();
  const gridClass = useStylesGrid();

  const CoursesItems = props.courses.map((e, i) => {
    return (
      <Grid
        item
        xs={12}
        sm={6}
        md={4}
        key={`product-${i}`}
        className={"product-card"}
      >
        <Card
          id={i === 1 ? "course" : "c"}
          className={`${gridClass.root} ${
            i === 1 ? "main-course" : "secondary-course"
          }`}
        >
          {/* <CardActionArea></CardActionArea> */}

          {/* {e.offerprice !== e.actprice ? (
              <div className="a">
                <img
                  src={Tag}
                  height={80}
                  width={70}
                  className="tag"
                  alt={e.coursename}
                />
                <p className="discount">20% off</p>
              </div>
            ) : null} */}
          <CardMedia
            className={`${gridClass.media} banner-image`}
            image={e.image}
            title={e.coursename}
          />

          <CardContent>
            <Typography gutterBottom variant="h6" component="h4">
              {e.coursename}
            </Typography>
            <Typography variant="body2" color="textSecondary" component="p">
              <small>. AGE 8-16</small> <small>. AGE 8-16</small>
              {i === 1 ? <span className="status-badge">New</span> : null}
            </Typography>
            <Divider />
            <List component="nav" aria-label="main mailbox folders">
              {e.features.map((f) => {
                return (
                  <ListItem key={`${f.title + i}-list-item`}>
                    <ListItemIcon>
                      <img src={f.icon} height={20} width={20} alt=".." />
                    </ListItemIcon>
                    <ListItemText secondary={f.title} />
                  </ListItem>
                );
              })}
            </List>
          </CardContent>
          <CardActions className="justify-content-space-between">
            {i !== 1 ? (
              <Button
                className="watch coming-soon"
                size="small"
                color="primary"
                onClick={() => alert("Coming Soon :), Stay Tuned!")}
              >
                Coming Soon
              </Button>
            ) : (
              <>
                <Button className="col-6 prize" size="small" color="primary">
                  &#x20B9; {e.offerprice}
                  {e.offerprice !== e.actprice ? <del>{e.actprice}</del> : ""}
                </Button>
                <Button
                  className="col-6 watch"
                  size="small"
                  color="primary"
                  href={`/courses/${e.id}`}
                >
                  Know More
                </Button>
              </>
            )}
          </CardActions>
        </Card>
      </Grid>
    );
  });

  return (
    <div className="container courses">
      <div className={classes.root}>
        <Grid container spacing={4}>
          {CoursesItems}
        </Grid>
      </div>
    </div>
  );
}
