function MotionBackground() {
  const defaultOptions = {
    loop: true,
    autoplay: true,
    animationData: motionGradient,
    rendererSettings: {
      preserveAspectRatio: "xMidYMid slice",
    },
  };

  return (
    <Lottie
      options={defaultOptions}
      height={400}
      width={400}
      isStopped={false}
      isPaused={false}
      className={"motion-background"}
    />
  );
}

