import React, { Component } from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { Workshopsobj } from "../Workshop/Workshopsobj";
import "./WorkshopSlider.css";
import { IconButton, Button } from "@material-ui/core";
import AI from "../../public/icons/AI";
import Android from "../../public/icons/Android";
import PythonIcon from "../../public/icons/PythonIcon";
import BotIcon from "../../public/icons/BotIcon";
import CIcon from "../../public/icons/CIcon";
import WebIcon from "../../public/icons/WebIcon";
import PrevIcon from "../../public/icons/circle-left.svg";
import ArrowRightAltIcon from "@material-ui/icons/ArrowRightAlt";

export default class CourseSlider extends Component {
  constructor(props) {
    super(props);
    this.gotoPrev = this.gotoPrev.bind(this);
    this.gotoNext = this.gotoNext.bind(this);
    this.state = {
      nav1: null,
      nav2: null,
      workshopIcons: [
        {
          icon: Android,
          name: "Courses",
        },
        {
          icon: PythonIcon,
          name: "Workshops",
        },
        {
          icon: CIcon,
          name: "Competitons",
        },
        {
          icon: AI,
          name: "RoboKits",
        },
        {
          icon: WebIcon,
          name: "Training",
        },
        // {
        //   icon: BotIcon,
        //   name: "Lab setup",
        // },
      ],
      settings1: {
        slidesToShow: 2,
        className: "slider variable-width custom-bg",
        focusOnSelect: true,
        swipeToSlide: true,
        slidesToScroll: 1,
        dots: false,
        infinite: true,
        variableWidth: true,
      },
      settings2: {
        swipeToSlide: true,
        dots: false,
        infinite: true,
      },
    };
  }

  componentDidMount() {
    this.setState({
      nav1: this.slider1,
      nav2: this.slider2,
    });
  }

  gotoPrev() {
    this.state.nav1.slickPrev();
    this.state.nav2.slickPrev();
  }

  gotoNext() {
    this.state.nav1.slickNext();
    this.state.nav2.slickNext();
  }

  render() {
    return (
      <>
        <div className="workshop-slider-container">
          <div className="ws-icons-slider">
            <IconButton onClick={this.gotoPrev} className="ws-prev-icon">
              <img src={PrevIcon} alt="workshop-prev" />
            </IconButton>
            <IconButton onClick={this.gotoNext} className="ws-next-icon">
              <img src={PrevIcon} alt="workshop-next" />
            </IconButton>
            <Slider
              className="slider-1"
              asNavFor={this.state.nav2}
              {...this.state.settings1}
              ref={(slider) => (this.slider1 = slider)}
            >
              {this.state.workshopIcons.map((icon) => {
                return (
                  <div key={icon.name} className="icon-slide-container">
                    <div className="icon-slide">
                      {/* <img src={icon.icon} alt={icon.name} /> */}
                      <icon.icon />
                      <p>{icon.name}</p>
                    </div>
                  </div>
                );
              })}
            </Slider>
          </div>

          <Slider
            className="slider-2"
            asNavFor={this.state.nav1}
            {...this.state.settings2}
            ref={(slider) => (this.slider2 = slider)}
          >
            {Workshopsobj.map((workshop) => {
              return (
                <div key={workshop.name} className="workshop-slide-container">
                  <div className="workshop-slide">
                    <div className="ws-details">
                      <div className="heading">
                        <h1>{workshop.name}</h1>
                        <div className="underline"></div>
                      </div>
                      <ul className="points">
                        {workshop.points.map((point, i) => {
                          return <li key={point + i}>{point}</li>;
                        })}
                      </ul>
                      <Button variant="contained" className="enroll-btn">
                        <span>Contact Us</span>{" "}
                        <span className="offer-text">
                          <ArrowRightAltIcon />
                        </span>
                      </Button>
                    </div>
                    <div className="ws-image">
                      <img src={workshop.image} alt={workshop.name} />
                    </div>
                  </div>
                </div>
              );
            })}
          </Slider>
        </div>
      </>
    );
  }
}
