import React, { Component } from "react";
import Robo from "../../public/images/Robo.webp";
import Code from "../../public/icons/code.svg";
import UI from "../../public/icons/ui.svg";
import Brain from "../../public/icons/brn.svg";
import { Button, Grid } from "@material-ui/core";
// import Workshop from "./Workshop";
import Courses from "../Courses/Courses";
import { withRouter, Link } from "react-router-dom";
import "./HomeComponentCss.css";
// import "../App.css";
import Footer from "../Footer/Footer";
import Reviews from "../Reviews/Reviews";

import { Workshopsobj } from "../Workshop/Workshopsobj";
import { Coursesobj } from "../Coursesobj";
import CourseSlider from "../WorkshopSlider/CourseSlider";
import YoutubeSection from "../YoutubeSection/YoutubeSection";
import { motion } from "framer-motion";
import Typewriter from "typewriter-effect";

class HomeComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      workshops: Workshopsobj,
      courses: Coursesobj,
    };
  }

  onResize() {
    if (window.innerWidth <= 900) {
      let vh = window.innerHeight * 0.01;
      document.documentElement.style.setProperty("--vh", `${vh}px`);
    }
  }

  componentWillMount() {
    if (window.innerWidth <= 900) {
      let vh = window.innerHeight * 0.01;
      document.documentElement.style.setProperty("--vh", `${vh}px`);
    }
  }

  componentDidMount() {
    this.onResize();
    window.addEventListener("resize", this.onResize);
    if (window.innerWidth <= 900) {
      let vh = window.innerHeight * 0.01;
      document.documentElement.style.setProperty("--vh", `${vh}px`);
    }
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.onResize);
  }

  render() {
    return (
      <>
        <section id="home" className="home">
          <Grid container className="section-container">
            <motion.img
              initial={{ y: 100, opacity: 0, scale: 0.6 }}
              animate={{ y: 0, opacity: 1, scale: 1 }}
              transition={{
                type: "spring",
                duration: 0.5,
                delay: 0.2,
              }}
              className="robo-image"
              src={Robo}
              alt="robofreaks"
            />
            <Grid item lg={6} md={12} sm={12} className="with-shadow">
              <div className="section-half">
                <img
                  className="robo-image-mobile"
                  src={Robo}
                  alt="robofreaks"
                />

                <div className="details">
                  <motion.h1
                    initial={{ y: 50, opacity: 0 }}
                    animate={{ y: 0, opacity: 1 }}
                    transition={{
                      type: "tween",
                      duration: 0.6,
                    }}
                  >
                    Enter the
                    <br />
                    world <br />
                    <span
                      style={{
                        display: "flex",
                        justifyContent: "flex-start",
                        alignItems: "center",
                      }}
                    >
                      of&nbsp;
                      <span
                        style={{
                          textDecoration: "underline",
                        }}
                      >
                        <Typewriter
                          options={{
                            strings: ["Robotics.", "Gaming.", "Coding."],
                            autoStart: true,
                            loop: true,
                          }}
                        />
                      </span>
                    </span>
                  </motion.h1>
                  <motion.p
                    initial={{ y: 50, opacity: 0 }}
                    animate={{ y: 0, opacity: 1 }}
                    transition={{
                      type: "tween",
                      duration: 0.6,
                      delay: 0.08,
                    }}
                  >
                    Join now and bring your imaginations to reality.
                  </motion.p>
                  <motion.div
                    initial={{ y: 50, opacity: 0 }}
                    animate={{ y: 0, opacity: 1 }}
                    transition={{
                      type: "tween",
                      duration: 0.6,
                      delay: 0.12,
                    }}
                    className="btns"
                  >
                    <Link style={{ textDecoration: "none" }} to="/courses/1">
                      <Button variant="contained" className="hero-btn">
                        Join Now
                      </Button>
                    </Link>
                    <Button variant="contained" className="hero-btn">
                      Demo
                    </Button>
                  </motion.div>
                </div>
                <img className="robo-image-tab" src={Robo} alt="robofreaks" />
              </div>
            </Grid>
            <Grid item lg={6} md={12} sm={12}>
              <div className="section-half course-list">
                <div className="course-container">
                  <motion.h3
                    initial={{ y: 50, opacity: 0 }}
                    animate={{ y: 0, opacity: 1 }}
                    transition={{
                      type: "tween",
                      duration: 0.6,
                      delay: 0.25,
                    }}
                  >
                    Special Courses
                  </motion.h3>
                  <div className="course-row1">
                    <motion.div
                      initial={{ y: 50, opacity: 0 }}
                      animate={{ y: 0, opacity: 1 }}
                      transition={{
                        type: "tween",
                        duration: 0.6,
                        delay: 0.27,
                      }}
                      className="course"
                    >
                      <img src={Code} alt="course" />
                    </motion.div>
                    <motion.div
                      initial={{ y: 50, opacity: 0 }}
                      animate={{ y: 0, opacity: 1 }}
                      transition={{
                        type: "tween",
                        duration: 0.6,
                        delay: 0.29,
                      }}
                      className="course"
                    >
                      <img src={Brain} alt="course" />
                    </motion.div>
                  </div>
                  <div className="course-row2">
                    <motion.div
                      initial={{ y: 50, opacity: 0 }}
                      animate={{ y: 0, opacity: 1 }}
                      transition={{
                        type: "tween",
                        duration: 0.6,
                        delay: 0.3,
                      }}
                      className="course"
                    >
                      <img src={UI} alt="course" />
                    </motion.div>
                    <motion.div
                      initial={{ y: 50, opacity: 0 }}
                      animate={{ y: 0, opacity: 1 }}
                      transition={{
                        type: "tween",
                        duration: 0.6,
                        delay: 0.32,
                      }}
                      className="course"
                    >
                      <p>+ More</p>
                    </motion.div>
                  </div>
                </div>
              </div>
            </Grid>
          </Grid>
        </section>
        <div id="land"></div>
        <CourseSlider />
        {/* <Workshop workshops={this.state.workshops} /> */}
        <div style={{ height: "10px" }} id="courses"></div>
        <Courses courses={this.state.courses} />
        <YoutubeSection />
        <Reviews />
        <Footer />
      </>
    );
  }
}
export default withRouter(HomeComponent);
